*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

/* Sign In Side */

.sign-in-main{
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.sign-in-main h2{
  font-size: 24px;
  margin-bottom: 10px;
}

.sign-in-main button{
  margin-top: 20px;
  width: 240px;
  height: 45px;
  color: white;
  font-size: 18px;
  cursor: pointer;
  border: none;
  background: #4285F4;
}

/* Sign In Side */

/* Navbar */

nav{
  width: 100%;
  height: 60px;
  background: transparent;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
}

nav .left-nav{
  background: transparent;
  padding: 10px 20px;
  display: flex;
  align-items: center;
}

nav .left-nav img{
  width: 120px;
  height: auto;
  object-fit: cover;
  padding-left: 20px;
}

nav .center-nav{
  width: 550px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 40px;
  margin-left: 100px;
}

nav .center-search{
  background: transparent;
  width: 93%;
  height: 40px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  padding-right: 10px;
  border: 2px solid #F0F0F0;
}

nav .center-search input{
  width: 100%;
  height: 40px;
  border: none;
  outline: none;
  background: transparent;
  font-size: 18px;
  text-indent: 20px;
}

nav .right-nav{
  background: transparent;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 400px;
}

.right-nav img{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}

nav .right-nav button{
  width: 80px;
  height: 40px;
  background: transparent;
  font-size: 16px;
  cursor: pointer;
  border: 1px solid #F0F0F0;
}

/* Navbar */


/* Main Side */

.main{
  display: flex;
}

::-webkit-scrollbar{
  width: 10px;
}

::-webkit-scrollbar-thumb{
  background: grey;
  border-radius: 10px;
}

.sidebar{
  flex: 0.6;
  /* background: green; */
  height: calc(100vh - 60px);
  overflow-y: auto;
}

.sidebar .sidebar-links li{
  width: 180px;
  padding: 8px 15px;
  margin: 15px;
  display: flex;
  align-items: center;
  background: transparent;
  border-radius: 10px;
  cursor: pointer;
}

.sidebar .sidebar-links li:hover{
  background: #F0F0F0;
}

.sidebar .sidebar-links li a{
  padding-left: 20px;
  text-decoration: none;
  color: black;
}

.sidebar .border-side{
  border-bottom: 1px solid #F0F0F0;
}

.sidebar .border-side p{
    padding: 10px 0 0 30px;
}

.sidebar .border-side img{
    width: 25px;
    height: 25px;
    border-radius: 50%;
}

.shortside{
  flex: 4;
  background: transparent;
  height: calc(100vh - 60px);
  overflow-y: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.shortside .btn{
   width: 50px;
   height: 50px;
   border-radius: 50%;
   border: none;
   cursor: pointer;
}

.shortside .down-btn{
  position: absolute;
  bottom: 40px;
  right: 40px;
}

.shortside .up-btn{
  position: absolute;
  top: 90px;
  right: 40px;
}

.shortside button:hover{
  background: #D9D9D9;
}

.main-video{
  width: 480px;
  height: 750px;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
}

.video-side{
  width: 420px;
  height: 750px;
  background: transparent;
  border-radius: 10px;
  position: relative;
}
.icon-side{
  width: 60px;
  height: 750px;
  background: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin-left: 5px;
}

.icon-side .icon-btn-side{
  background: transparent;
  margin-bottom: 25px;
  text-align: center;
}

.icon-side button {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: none;
  margin-bottom: 5px;
  text-align: center;
  line-height: 60px;
  cursor: pointer;
}

.icon-side button:hover{
  background: #D9D9D9;
}

.icon-side .add-side{
  width: 50px;
  height: 50px;
  background: transparent;
  border-radius: 10px;
}

.icon-side .add-side img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
  cursor: pointer;
}

.video-side video{
  width: 100%;
  height: 100%;
  border-radius: 10px;
  position: relative;
  object-fit: cover;
}

.video-side .video-navbar{
  position: absolute;
  z-index: 1;
  width: 410px;
  margin-top: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
}

.video-side .video-navbar button{
  border: none;
  cursor: pointer;
  background: transparent;
}

.video-side .progress{
  position: absolute;
  bottom: 0px;
  height: 3px;
  background: red;
  z-index: 2;
  margin-left: 6px;
}

.video-side .buffer{
  position: absolute;
  bottom: 0px;
  width: 97%;
  height: 3px;
  z-index: 2;
  margin-left: 6px;
}